import React, {useState, useEffect } from "react";
import Home from "./pages/Home";
import './App.css';
import Navbar from './components/Navbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Essentiels from './pages/Essentiels';
import BienEtre from './pages/BienEtre';
import Incidents from './pages/Incidents';
import Predictions from './pages/Predictions';
import MonEtablissement from './pages/MonEtablissement';
import ScrollToTop from "./style/components/ScrollToTop";
import RotateLoader from "react-spinners/RotateLoader";




function App() {
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
    }, 2000)
  }, [])
  return (
    <div className="App">
      {
        loading ? (

        <RotateLoader
        size={20}
        margin={2}
        color={"#4EAABF"}
        loading={loading}
        speedMultiplier={1}
        />
        ) 
        : ( 
      <header className="App-Header">
          <Router>
            <ScrollToTop/>
            <Navbar />
            <Switch>
              <Route path='/' exact component={Home} />
              <Route path='/etablissement' component={MonEtablissement} />
              <Route path='/essentiel' component={Essentiels} />
              <Route path='/bien-etre' component={BienEtre} />
              <Route path='/incidents' component={Incidents} />
              <Route path='/predictions' component={Predictions} />
            </Switch>
          </Router> 
        </header>
        )}</div>
  );
}

export default App;
