import * as d3 from "d3";
import React, { useRef, useEffect, useState } from "react";
import styles from  '../style/index.scss';


function Bienetre() {

    console.log("init");
    const [data] = useState([
        { property: 'Opérations NIR', value: 10 },
        { property: 'Opératuins PEARL', value: 3 },
        { property: 'Mandats NPM', value: 4 },
    ])
    console.log("data checked");

    const svgRef = useRef();
    useEffect(() => {
        // set up container
        const w = 400;
        const h = 400;
        const radius = w / 2;
        const svg = d3.select(svgRef.current)
        .attr('width', w)
        .attr('height', h)
        .style('overflow', 'visible')
        .style('margin-top', '200px')
        .style('margin-left', '600px')



        console.log(" contenaire checked");

        //set up chart
        const formattedData = d3.pie().value(d => d.value)(data);
        const arcGenerator = d3.arc().innerRadius(0).outerRadius(radius);
        const color = d3.scaleOrdinal().range(d3.schemeSet2);
        console.log(" chart checked");

        //set up svg data
        //g permet de centre l'origine du graphe
        let g = svg.attr('height', h)
        .append('g')
        .attr('transform', 'translate(' + w/2 +  ',' + h/2 +')');


        g.selectAll()
        .data(formattedData)
        .join('path')
        .attr('d', arcGenerator)
        .attr('fill', d => color(d.value));

    }, [data]);

    return (
        <div className={styles.container}>
            <svg ref={svgRef}  ></svg>
        </div>
    )

function BienEtre(props) {
  return (props.trigger) ? (
    <div className='popup'>
      <div className='popup-inner'>
        <button className='close-btn' onClick={() => props.setTrigger(false)}>fermer</button>
        { props.children }
      </div>
    </div>
  ) : "";
}
}

export default Bienetre;
